function makeNew() {
    const db = new sql.Database()
    let sqlstr = `CREATE TABLE IF NOT EXISTS 
            product(
                code_id CHAR NOT NULL PRIMARY KEY,
                title   CHAR NOT NULL);`
    db.run(sqlstr)
    sqlstr = `INSERT INTO product VALUES('4103330017369', 'SuperFloss')`
    db.run(sqlstr)
    sqlstr = `CREATE TABLE IF NOT EXISTS 
            expiry(
                expiry_id   INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, 
                code_id     CHAR NOT NULL, 
                date        TEXT NOT NULL,
                FOREIGN KEY(code_id) REFERENCES product(code_id));`
    db.run(sqlstr);
    return db
}

function getProduct(db, rawValue) {
    console.log(`trying to find ${rawValue}`)
    const stmt = db.prepare("SELECT title FROM product WHERE code_id=:code")
    // Bind values to the parameters and fetch the results of the query
    stmt.bind([rawValue])
    //while (stmt.step())
    //    console.log(stmt.get()) // Will print {a:1, b:'world'}
    return stmt.step() ? stmt.get() : ``
}

function getExpiries(db, code) {   
    const result = code ?
        db.exec(`
            SELECT expiry_id, expiry.code_id, title, date from expiry 
            INNER JOIN product ON product.code_id = expiry.code_id
            WHERE code_id=?`, code)
        : db.exec(`
            SELECT expiry_id, expiry.code_id, title, date from expiry 
            INNER JOIN product ON product.code_id = expiry.code_id
            ORDER BY date
            `)
    return result.length > 0 ? result[0].values : []
}

function deleteExpiry(db, id) {
    const result = db.exec(`DELETE FROM expiry WHERE expiry_id=?`, [id])
}

const ensureProduct = function(db, {code, title}) {
    const stmt = db.prepare(`SELECT * FROM product WHERE code_id=:code`)
    stmt.bind([code])
    if (!stmt.step()) {
        db.run(`INSERT INTO product VALUES (?, ?)`, [code, title]) //code_id=:code, title=:title)`)
    } else {
        console.log(`product exists`)
    }
}

const addExpiry = function(db, code, expiryDate) {
    db.run(`INSERT INTO expiry VALUES (NULL, ?, ?)`, [code, expiryDate])
}

function addProductWithExpiryDate(db, {code, title}, expiryDate) {
    ensureProduct(db, {code, title})
    addExpiry(db, code, expiryDate)
}

// https://en.wikipedia.org/wiki/International_Article_Number#Calculation_of_checksum_digit
function isCodeValid(rawValue) {
    let sum = 0
    const lastIndex = rawValue.length - 1
    for (let i = 0; i != lastIndex; i += 1) {
        sum += (i % 2 ? 3 : 1) * Number.parseInt(rawValue.charAt(i))
    }
    return (sum + Number.parseInt(rawValue.charAt(lastIndex))) % 10 === 0
}