let db
let fileName = `my.db`

const states = {
    INIT: 0,
    DB: 1,
    ADD: 2,
    SAVE: 3
}

let state = states.INIT
let idToDisplay = [];

(async function () {
    // Read db
    //const readBtn = document.body.appendChild(document.createElement("button"))
    //readBtn.innerHTML = "Open"
    //readBtn.addEventListener('click', readDb);

    //const readBtn = document.body.querySelector(`#fileName`) //appendChild(document.createElement("input"))
    //readBtn.setAttribute(`type`, `file`)
    
    window.addEventListener(`load`, function(event) {
        for (const x of document.querySelectorAll(`*`)) {
            idToDisplay[`#${x.id}`] = x.style.display
        }
        transitTo(states.INIT)
    })
    
    

    const body = document.body

    body.querySelector(`#newBtn`).addEventListener(`click`, 
        event => { 
            db = makeNewDb(); 
            transitTo(states.DB)
        }
    )
    const f =  async function(event) { 
        fileName = body.querySelector(`#fileName`).files[0]
        db = await loadDbFromFile(fileName)
        info(`Loaded ${fileName.name}.`)
        transitTo(states.DB)
    }
    body.querySelector(`#fileName`).addEventListener(`change`, f)  

    body.querySelector(`#saveBtn`).addEventListener(`click`, 
        async function(event) { 
            transitTo(states.SAVE)
        }
    ) 
    body.querySelector(`#downloadBtn`).addEventListener(`click`,
        function(event) {
            transitTo(states.INIT)
        }
    )
    // const detectBtn = document.body.appendChild(document.createElement("button"))
    // detectBtn.innerHTML = `Detect`
    // detectBtn.setAttribute(`id`, `detectBtn`)
    // detectBtn.addEventListener(`click`, _ => { detect() })

})()

