function hide(id) {
    document.querySelector(id).style.display = `none`// = `hidden`
}
function unhide(id) {
    document.querySelector(id).style.display = idToDisplay[id] //`none` //visibility = `visible`
}

function showExpiries() {
    const expiries = getExpiries(db)
    const list = document.querySelector(`#list`)
    list.innerHTML = ``
    for (const expiry of expiries) {
        const e = document.createElement(`div`)
        list.appendChild(e)
        e.innerHTML += expiry[0] + " " + expiry[1] + " " + expiry[2]
        const deleter = document.createElement(`button`)
        deleter.innerHTML = `${expiry[3]} delete`
        deleter.addEventListener(`click`, function(event) {
            deleteExpiry(db, expiry[0])
            console.log(`deleting expiry ${expiry[0]}`)
            transitTo(states.DB)
        })
        e.appendChild(deleter)
    }
}

function transit() {
    switch (state) {
        case states.INIT:
            state = states.DB
            transitTo(states.DB)
            console.log(`transiting to DB`)            
        break
        case states.DB:
        break
    }
}

function transitTo(s, ...rest) {
    switch (s) {
    case states.INIT:
        hide(`#downloadBtn`)
        hide(`#db`)
        hide(`#add`)    
        document.querySelector(`#fileName`).value = ``
        unhide(`#init`)
        break
    case states.DB:
        const body = document.body
        hide(`#add`)
        hide(`#init`)
        hide(`#downloadBtn`)

        unhide(`#db`)
        showExpiries(db)
        detect()
        break
    case states.ADD:
        unhide(`#add`)
        showAdd(...rest)
        break
    case states.SAVE:
        /*await*/ exportDb()
        break                
    }
}

function onDeviceReady() {
    // check compatibility
    if (!("BarcodeDetector" in globalThis)) {
        console.log("Barcode Detector is not supported by this browser.");
    } else {
        console.log("Barcode Detector supported!");

        // create new detector
        const barcodeDetector = new BarcodeDetector({
            formats: ["code_39", "codabar", "ean_13"],
        });
    }
    detect()
}

async function detect() {
    const body = document.body
    const scan = body.querySelector(`#scan`)

    try {
        const mediaStream = await navigator.mediaDevices.getUserMedia({
            video: { facingMode: 'environment' }
        });
        scan.srcObject = mediaStream
        scan.autoplay = true
        // videoList.before(scan);
    } catch (ex) {
        console.log(ex)
        transitTo(states.ADD, `barcode`, `title`)
        return
    }


    let barcodeDetector
    let itemsFound = []
    const list = document.body.appendChild(document.createElement('list'))
    if (`BarcodeDetector` in globalThis) {
        barcodeDetector = new BarcodeDetector({
            formats: ["code_39", "codabar", "ean_13"]
        });
    } else {
        console.log("NO BARCODE DETECTOR")
        barcodeDetector = {
            detect(x) {
                return new Promise((resolve, reject) => {
                    setTimeout(resolve([{
                        rawValue: `4103330017369`
                    }]), 1)
                })
            }
        }
    }

    let isRenderingDone = false;
    (function renderLoop() {
        if (isRenderingDone)
            return
        requestAnimationFrame(renderLoop);
        render();
    })();

    async function render() {
        const barcodes = await barcodeDetector.detect(scan)
        try {
            barcodes.forEach(function (barcode) {
                if (itemsFound.includes(barcode.rawValue))
                    return

                const code = barcode.rawValue
                if (!isCodeValid(code))
                    return

                isRenderingDone = true
                
                //itemsFound.push(rawValue);
                // const li = document.createElement('li');
                // li.innerHTML = rawValue + (!isCodeValid(rawValue) ? `(invalid)` : ``)
                // list.appendChild(li);

                // Show form: Title (pre-fill if product exists), Expiration date
                
                const title = getProduct(db, code)
                transitTo(states.ADD, code, title)

                // // https://yari-demos.prod.mdn.mozit.cloud/en-US/docs/Web/Guide/AJAX/Getting_Started
                // const httpRequest = new XMLHttpRequest();
                // httpRequest.onreadystatechange = () => {
                // if (httpRequest.readyState === XMLHttpRequest.DONE) {
                //        console.log(httpRequest.responseText)
                //     // Everything is good, the response was received.
                // } else {
                //     // Not ready yet.
                // }
                // }

                //console.log("send request: " + `https://opengtindb.org/?ean=${barcode.rawValue}&cmd=query&queryid=${400000000}`)
            })
            //httpRequest.open(`GET`, `https://opengtindb.org/?ean=${barcode.rawValue}&cmd=query&queryid=${400000000}`, true)
            //httpRequest.send()
        } catch (ex) {
            console.error(ex)
        }

    }
}

function showAdd(code, title) {
    document.querySelector(`#code`).value = code
    document.querySelector(`#title`).value = title
    document.querySelector(`#btnAdd`).onclick = function(event) {
        const date = document.querySelector(`#date`).value
        if (!date) {
            info(`Date is invalid.`)
            return
        }
        addProductWithExpiryDate(
            db, 
            {code: document.querySelector(`#code`).value, title: document.querySelector(`#title`).value}, 
            date)
        info(`Successfully added.`)
        transitTo(states.DB)
    }
    document.querySelector(`#btnIgnore`).onclick = function(event) {
        info(`Ignored.`)
        transitTo(states.DB)
    }    
}

function info(message) {
    document.querySelector(`#info`).innerHTML = message
}